<template>
	<div class="park-total">

		<div class="CarTotal">
			<img src="../../../../assets/img/title/wl-sdqjk.png"/>
			<div style="height: 200px;padding: 0;position: relative;">
				<pieAll ids="power" :data='seriesData' unit="个"> </pieAll>
				<div class="msg">
					<h1>今日用电</h1>
					<p><big>6980</big><small>KW.H</small></p>
					<h1>昨日用电</h1>
					<p class="error"><big class="green">20</big><small>KW.H</small></p>
				</div>
			</div>
		</div>
		<img src="@/assets/img/title/wl-yqysl.png"/>
		<div class="ysl">
		<el-row :gutter="30">
			<el-col :span="8">
				<div class="card m-b-30">
					<p>今日用水量</p>
					<p class="color-gradient"><big class="color-gradient">20.2</big>&nbsp;&nbsp;T</p>
				</div>
			</el-col>
			<el-col :span="8">
				<div class="card m-b-30">
					<p>昨日用水量</p>
					<p class="color-gradient"><big class="color-gradient">12.7</big>&nbsp;&nbsp;T</p>
				</div>
			</el-col>
			<el-col :span="8">
				<div class=" m-b-30">
					<p>本月用水量</p>
					<p class="color-gradient"><big class="color-gradient">1209</big>&nbsp;&nbsp;T</p>
				</div>
			</el-col>
		</el-row>
		</div>
		<img src="@/assets/img/title/wl-yqyql.png"/>
		<div class="barTotal">
			<myLine ids="myLine" :data="carTotalHour"></myLine>
		</div>
	</div>
</template>

<script>
	import percentPie from '@/components/module/echarts/percent'
	import myLine from '@/components/module/echarts/line'
	import pieAll from '@/components/module/echarts/percentAll'
	export default{
		components:{
			percentPie,
			myLine,
			pieAll
		},
		data(){
			return{
				seriesData:[{
					name:"本月用电",
					value:436
				}],
				carTotalHour:{
					title:"园区用气量",
					data:[755,745,722,768,751,774],
					dataAxis:['4/hr','8/hr','12/hr','16/hr','20/hr','24/hr'],
					unit:'辆',
					total:886
				},
				carTotal:[{
				    'name': '总停车位数量',
				    'value': 886
				}],
				carEd:[{
				    'name': '已使用车位数量',
				    'value': 725
				}],
				caring:[{
				    'name': '剩余车位数量',
				    'value': 161
				}],
			}
		}
	}
</script>
<style scoped="scoped" lang="less">
	.park-total{padding-top: 0;}
	.park-total li{
	    width: 50%;
	    display: inline-block;
	    margin-left: -4px;
	    box-sizing: border-box;
	    padding-left: 0;
	}
	.park-total li:last-of-type big{
		font-size: 36px;
	}
	.CarTotal{
		margin: 10px 0 40px;
		width: 100%;
		height: auto;
		p{
			font-size:17px;
			color: #FFFFFF;
			text-align: center;
		}
	}
	big{
		background: linear-gradient(to top, #0072ff, #00fff0);
        -webkit-background-clip: text;
		color: transparent;
		font-family: 'DINOT';
		font-size: 32px;
	}
	.carText{
		display: inline-block;
		width: 145px;
		padding-left: 40px;
		box-sizing: border-box;
	 	li{
	        list-style: none;
	        text-align: left;
	        position: relative;
	        width: 100%;
	        margin-bottom: 20px;
	        p{
	            margin: 0;
	            font-size:18px;
	            color: #FFFFFF;
	        }
	        big{
	            background: linear-gradient(to top,#0072ff, #00c0ff);
	            -webkit-background-clip: text;
	            color: transparent;
	            font-size: 36px;
	            line-height: 40px;
	            font-family: "DINOT";   
	             font-weight: bold;
	        }
	        small{
	            font-size: 16px;
	            padding-left: 10px;
	            color:#a8a8a8;
	        }
	        
	    }
	}
	p{
		color: #fff;
	}
	.percent{
		height: 150px!important;
	}
	.barTotal{
		height: 280px;
	}
	.msg{
		position: absolute;
		top: 20px;
		left: 60%;
	}
	.msg h1{
		font-size: 18px;
		color: #FFFFFF;
		font-weight: normal;
	}
	.msg p{
		margin-bottom: 20px;
	}
	.msg big{
		font-size: 36px;
		line-height: 40px;
		font-weight: bold;
	}
	.msg small{
		font-size: 18px;
		color: #32e0ff;
		margin-left: 20px;
	}
	.card{
		position: relative;
	}
	.card::after{
		content:'';
		height: 80%;
		display: inline-block;
		width: 1;
		position: absolute;
		right: 0;
		top: 10%;
		border-right: 1px dashed rgba(255,255,255,.3);
	}
	.ysl{
		margin-bottom: 40px;
		padding-top: 20px;
	}
	/*.ysl .card:last-of-type::after{
		border: none;
	}*/
</style>


