<template>
	<div class="right-park-total right-park">
		<img src="../../../../assets/img/title/wl-ktsyqk.png"/>
		<el-row :gutter="10" class="percent-box" style="margin-top: 20px;">
			<el-col :span="8" v-for="(item,index) in FaceTotal" :key="index">
				<div style="height: 125px;">
					<circleHalf :ids="'face'+index" :data='[item]' :total="58"> </circleHalf>
				</div>
				<p style="text-align: center;margin: 0;color: #fff;">{{item.name}}</p>
			</el-col>
		</el-row>
		<img src="../../../../assets/img/title/wl-ypjkthn.png" style="margin-top: 40px;"/>
		<div class="barTotal" style="height: 260px;">
			<myLine ids="myLines" :colorList="[]" :data="carTotalHour"></myLine>
		</div>
		<img src="@/assets/img/title/wl-dtyxqk.png" style="margin-top: 30px;"/>
		<div class="ysl">
			<el-row :gutter="30">
				<el-col :span="12">
					<div class="card m-b-30">
						<p>电梯总数</p>
						<p class="color-gradient"><big class="color-gradient">20</big>&nbsp;&nbsp;部</p>
					</div>
				</el-col>
				<el-col :span="12">
					<div class=" m-b-30">
						<p>电梯故障数</p>
						<p class="color-gradient"><big class="color-gradient">2</big>&nbsp;&nbsp;部</p>
					</div>
				</el-col>
				<el-col :span="24">
				<div style="height: 20px;"></div>
				</el-col>
				<el-col :span="12">
					<div class="card m-b-30">
						<p>运行电梯数</p>
						<p class="color-gradient"><big class="color-gradient">280</big>&nbsp;&nbsp;部</p>
					</div>
				</el-col>
				<el-col :span="12">
					<div class=" m-b-30">
						<p>今日报警数</p>
						<p class="color-gradient"><big class="color-gradient">4</big>&nbsp;&nbsp;部</p>
					</div>
				</el-col>
			</el-row>
		</div>
	</div>
	
</template>

<script>
import circleHalf from '@/components/module/echarts/circleHalf'
import myLine from '@/components/module/echarts/line'
	export default{
		components:{
			circleHalf,
			myLine
		},
		data(){
			return{
				FaceTotal:[{
				    'name': '最大负荷',
				    'value': 645,
				    'unit':'KW/H'
				},{
				    'name': '当前负荷',
				    'value': 486,
				    'unit':'KW/H'
				},{
				    'name': '故障入口数',
				    'value': 16,
				    'unit':'个'
				}],
				carTotalHour:{
					title:"月平均空调能耗",
					data:[705,445,1022,1768,651,1774],
					dataAxis:['2/月','4/月','6/月','8/月','10/月','12/月'],
					unit:'辆',
					total:886
				},
			}
		}
	}
</script>
<style scoped="scoped">
	big{
		background: linear-gradient(to top, #0072ff, #00fff0);
        -webkit-background-clip: text;
		color: transparent;
		font-family: 'DINOT';
		font-size: 32px;
	}
	p{
		color: #fff;
	}
	.card{
		position: relative;
		text-align: left;
	}
	.card::after{
		content:'';
		height: 80%;
		display: inline-block;
		width: 1;
		position: absolute;
		right: 0;
		top: 10%;
		border-right: 1px dashed rgba(255,255,255,.3);
	}
	.ysl{
		margin-bottom: 40px;
		padding-top: 20px;
		text-align: left;
	}
</style>


